/* COMMON */
* {
  margin: 0;
  box-sizing: border-box;
}

/* NAVBAR */
.noDeco {
  text-decoration: none;
  color: #eee;
}
.noDeco:hover {
  color: #495057
}

/* 종선택 */
.selection {
  text-align: center;
  font-size: 20px;
}

.species {
  width: 50%;
  padding: 5px;
  font-size: 20px;
  font-weight: 400;
  color: #495057;
  background-color: #fff;
  border: 1px solid #ced4da;
  border-radius: 5px;
  margin: 20px;
}

/* 플렉스 할라고 */
.tableAndGraph {
  display: flex;
  justify-content: center;
}

/* 표관련 CSS */
table.type01 {
  border-collapse: collapse;
  text-align: center;
  line-height: 1.5;
}
table.type01 th {
  width: 90px;
  padding: 10px;
  font-weight: bold;
  border: 1px solid #ccc;
  color: black;
  text-align: center;
}
table.type01 td {
  width: 200px;
  padding: 10px;
  color: #495057;
  border: 1px solid #ccc;
  text-align: center;
}
#Na {
  color: royalblue;
}
#K {
  color: blueviolet;
}
#Cl {
  color: yellowgreen;
}
#HCO3 {
  color: crimson;
}
#AG {
  color: orange;
}
table input {
  width: 70px;
  text-align: center;
}

/* 그래프관련 */
.graph {
  font-size: 20px;
  display: flex;
  text-align: center;
  align-items: flex-end;
  font-weight: 700;
  margin-left: 10px;;
}
.plus {
  width: 100px;
  margin-right: 5px;
}
.minus {
  width: 100px;
}
.Na {
  background-color: royalblue;
  height: calc(148px * 2);
  line-height: calc(148px * 2);;
}
.K {
  background-color: blueviolet;
  height: calc(4.5px * 2);
  line-height: calc(4.5px * 2);
}
.AG {
  background-color: orange;
  height: calc((148px + 4.5px - 22.5px - 114.5px) * 2);
  line-height: calc((148px + 4.5px - 22.5px - 114.5px) * 2);
}
.HCO3 {
  background-color: crimson;
  height: calc(22.5px * 2);
  line-height: calc(22.5px * 2);
}
.Cl {
  background-color: yellowgreen;
  height: calc(114.5px * 2);
  line-height: calc(114.5px * 2);
}

/* 분석버튼 */
.anal-btn {
  text-align: center;
}

/* 수정컴퍼넌트 */
.black-background{
  background-color: rgba(0,0,0,.5);
  position: absolute;
  width: 100%;
  height: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.modify {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  margin: 0;
  padding: 0;
  text-align: center;
}
.Modify .table {
  margin: 5px 0;
}