.contain{
  padding-top: 100px;
  width: 60vw;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  text-align: center;
  .alert {
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%, 0);
    font-weight: 700;
    font-size: 30px;
    background-color: rgb(228, 185, 245);
    color: white;
  }
  .toDoList {
    margin: 0;
    padding: 0;
    .toDoAndButton
    {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px dashed #495057;
      span {
        padding: 10px
      }
    }
    .toDoAndButton:last-child {
      border: none;
    }
  }
  .toDoAddEl {
    display: flex;
    justify-content: center;
    .toDoInput {
      width: 70%;
    }
  }
  .back {
    margin-top: 10px;
  }
}